import { World } from "ecsy";
import { LifeTimeData } from "./DataComponents/LifeTimeData";
import { MassData } from "./DataComponents/MassData";
import { RotationData } from "./DataComponents/RotationData";
import { TransformData } from "./DataComponents/TransformData";
import { VelocityData } from "./DataComponents/VelocityData";
import { Vector2 } from "./Math/Vector2";
import { BallRenderSystem } from "./Systems/BallRenderSystem";
import { ClearCanvasSystem } from "./Systems/ClearCanvasSystem";
import { DebugRenderSystem } from "./Systems/DebugRenderSystem";
import { DraggableHighlightRenderSystem } from "./Systems/DraggableHighlightRenderSystem";
import { DraggableSystem } from "./Systems/DraggableSystem";
import { FanPhysicsSystem } from "./Systems/FanPhysicsSystem";
import { FanRenderSystem } from "./Systems/FanRenderSystem";
import { LifeTimeSystem } from "./Systems/LifeTimeSystem";
import { SpawnBallSystem } from "./Systems/SpawnBallSystem";
import { VelocityMoveSystem } from "./Systems/VelocityMoveSystem";
import { BallTag } from "./TagComponents/BallTag";
import { DebugTag } from "./TagComponents/DebugTag";
import { DraggableHighlightTag } from "./TagComponents/DraggableHighlightTag";
import { DraggableTag } from "./TagComponents/DraggableTag";
import { FanTag } from "./TagComponents/FanTag";
import { SpawnBallTag } from "./TagComponents/SpawnBallTag";

export const init = (world: World) => {
  // Get the main canvas.
  const mainCanvas = document.getElementById("mainCanvas") as HTMLCanvasElement;

  // Resize the canvas to the style size.
  mainCanvas.width = mainCanvas.clientWidth;
  mainCanvas.height = mainCanvas.clientHeight;

  // Get rotation slider.
  const rotationSlider = document.getElementById(
    "rotationSlider"
  ) as HTMLInputElement;

  // Register TagComponents.
  world
    .registerComponent(FanTag)
    .registerComponent(BallTag)
    .registerComponent(SpawnBallTag)

    .registerComponent(DraggableTag)
    .registerComponent(DraggableHighlightTag)
    .registerComponent(DebugTag);

  // Register DataComponents.
  world
    .registerComponent(TransformData)
    .registerComponent(RotationData)
    .registerComponent(VelocityData)
    .registerComponent(MassData)
    .registerComponent(LifeTimeData);

  // Clear canvas system.
  world.registerSystem(ClearCanvasSystem, {
    mainCanvas: mainCanvas,
  });

  // Register Game Play Systems.
  world
    .registerSystem(DraggableSystem, {
      mainCanvas: mainCanvas,
      rotationSlider: rotationSlider,
    })
    .registerSystem(VelocityMoveSystem)
    .registerSystem(FanPhysicsSystem)
    .registerSystem(LifeTimeSystem)
    .registerSystem(SpawnBallSystem);

  // Register Render Systems.
  world
    .registerSystem(FanRenderSystem, {
      mainCanvas: mainCanvas,
    })
    .registerSystem(BallRenderSystem, {
      mainCanvas: mainCanvas,
    })
    .registerSystem(DebugRenderSystem, {
      mainCanvas: mainCanvas,
    })
    .registerSystem(DraggableHighlightRenderSystem, {
      mainCanvas: mainCanvas,
    });

  // Randomly generate 3 fans.
  for (let i = 0; i < 3; i++) {
    const fanEntity = world.createEntity();
    fanEntity
      .addComponent(FanTag)
      .addComponent(TransformData, {
        position: new Vector2(
          Math.random() * mainCanvas.width,
          Math.random() * mainCanvas.height
        ),
      })
      .addComponent(RotationData, {
        rotation: Math.random() * 2 * Math.PI,
      })
      .addComponent(DraggableTag);
  }

  // Ball spawn point at the center of the canvas.
  const spawnPoint = new Vector2(mainCanvas.width / 2, mainCanvas.height / 2);
  const spawnBallEntity = world.createEntity();
  spawnBallEntity
    .addComponent(SpawnBallTag)
    .addComponent(TransformData, {
      position: spawnPoint,
    })
    .addComponent(RotationData, {
      rotation: (3 / 2) * Math.PI,
    })
    .addComponent(DraggableTag);
};
