import { cloneClonable, copyCopyable, createType } from "ecsy";

export class Vector2 {
  x: number;
  y: number;

  constructor(x: number, y: number) {
    this.x = x;
    this.y = y;
  }

  set(x: number, y: number): Vector2 {
    this.x = x;
    this.y = y;
    return this;
  }

  copy(v: Vector2): Vector2 {
    this.x = v.x;
    this.y = v.y;
    return this;
  }

  clone(): Vector2 {
    return new Vector2(this.x, this.y);
  }

  add(v: Vector2): Vector2 {
    let out = new Vector2(this.x + v.x, this.y + v.y);
    return out;
  }

  sub(v: Vector2): Vector2 {
    let out = new Vector2(this.x - v.x, this.y - v.y);
    return out;
  }

  scale(s: number): Vector2 {
    let out = new Vector2(this.x * s, this.y * s);
    return out;
  }

  magnitude(): number {
    return Math.sqrt(this.x * this.x + this.y * this.y);
  }
}

export const Vector2Type = createType({
  name: "Vector2",
  default: new Vector2(0, 0),
  copy: copyCopyable<Vector2>,
  clone: cloneClonable<Vector2>,
});
