import { Attributes, System, SystemQueries } from "ecsy";
import { TransformData } from "../DataComponents/TransformData";
import { BallTag } from "../TagComponents/BallTag";

export class BallRenderSystem extends System {
  static queries: SystemQueries = {
    balls: {
      components: [BallTag, TransformData],
    },
  };

  mainCanvas: HTMLCanvasElement;
  canvasContext: CanvasRenderingContext2D;

  init(attributes?: Attributes): void {
    this.mainCanvas = attributes.mainCanvas;
    this.canvasContext = this.mainCanvas.getContext("2d");
  }

  execute(delta: number, time: number): void {
    this.queries.balls.results.forEach((entity) => {
      const transformData = entity.getComponent(TransformData);

      this.canvasContext.save();
      this.canvasContext.translate(
        transformData.position.x,
        transformData.position.y
      );
      this.canvasContext.fillStyle = "black";
      this.canvasContext.beginPath();
      this.canvasContext.arc(0, 0, 10, 0, Math.PI * 2);
      this.canvasContext.closePath();
      this.canvasContext.fill();
      this.canvasContext.restore();
    });
  }
}
