import { Attributes, System, SystemQueries } from "ecsy";
import { TransformData } from "../DataComponents/TransformData";
import { VelocityData } from "../DataComponents/VelocityData";

export class VelocityMoveSystem extends System {
  static queries: SystemQueries = {
    entities: {
      components: [VelocityData, TransformData],
    },
  };

  init(attributes?: Attributes): void {}

  execute(delta: number, time: number): void {
    this.queries.entities.results.forEach((entity) => {
      const velocity = entity.getComponent(VelocityData);
      const transform = entity.getMutableComponent(TransformData);

      transform.position.x += velocity.velocity.x * delta;
      transform.position.y += velocity.velocity.y * delta;
    });
  }
}
